import { useEffect, useState } from "react";


export default function Connect(){

const [posts,setPosts]=useState();
const [status,setStatus]=useState("Loading");

useEffect(()=> {

async function loadPost(){
try{
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/x-www-form-urlencoded");
    
    var urlencoded = new URLSearchParams();
    urlencoded.append("grant_type", "client_credentials");
    urlencoded.append("client_id", "uzXNEStfxdPTdGPMAGo9oeGbkXREJjOe");
    urlencoded.append("client_secret", "lW7Tjua7KNILkt29");
    
    var requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: urlencoded,
      redirect: 'follow'
    };
    
   const response= await fetch("https://test.api.amadeus.com/v1/security/oauth2/token", requestOptions)
     let tk=await response.json();
     console.log(tk);
     console.log(tk.access_token);
     setPosts(tk.access_token);

    }catch (error){
    console.error(error);
    setStatus("Error");
    }
}
     loadPost();   
    
 }, []);


//if (status === "Loading") return <span>Loading...{posts}</span>;
//if (status === "error") return <span>Error</span>;



return(
   <div>
   <span>{posts}</span>
   </div>
);


}
