import '/node_modules/primeflex/primeflex.css';

//import ReactDOM from 'react-dom';
import React, { useState, useEffect } from 'react';
import { Calendar } from 'primereact/calendar';
import { addLocale } from 'primereact/api';
import 'primeicons/primeicons.css';
import 'primereact/resources/themes/lara-light-indigo/theme.css';
import 'primereact/resources/primereact.css';
import { AutoComplete } from 'primereact/autocomplete';
import { CountryService } from './CountryService';
//import { FaPlaneDeparture, FaPlaneArrival } from "react-icons/fa";
//import 'index.css';

import { Card } from 'primereact/card';
import { Button } from 'primereact/button';
import Connect from '../api/api.jsx';
//import CardD from './tarjeta';

//import "/react/style-prop-object";
//import Buscador from "../components/buscador/Buscador";

export default function Buscar() {
  
    let today = new Date();
    let month = today.getMonth();
    let year = today.getFullYear();
    let prevMonth = (month === 0) ? 11 : month - 1;
    let prevYear = (prevMonth === 11) ? year - 1 : year;
    let nextMonth = (month === 11) ? 0 : month + 1;
    let nextYear = (nextMonth === 0) ? year + 1 : year;
    
    const [date2, setDate2] = useState(null);
    const [date3, setDate3] = useState(null);
    const [countries, setCountries] = useState([]);
    const [selectedCountry1, setSelectedCountry1] = useState(null);
    const [selectedCountry2, setSelectedCountry2] = useState(null);
    const [filteredCountries, setFilteredCountries] = useState(null);
  
    const countryservice = new CountryService();
    
    let minDate = new Date();
    minDate.setMonth(prevMonth);
    minDate.setFullYear(prevYear);

    let maxDate = new Date();
    maxDate.setMonth(nextMonth);
    maxDate.setFullYear(nextYear);

    addLocale('es', {
        firstDayOfWeek: 1,
        dayNames: ['domingo', 'lunes', 'martes', 'miércoles', 'jueves', 'viernes', 'sábado'],
        dayNamesShort: ['dom', 'lun', 'mar', 'mié', 'jue', 'vie', 'sáb'],
        dayNamesMin: ['D', 'L', 'M', 'X', 'J', 'V', 'S'],
        monthNames: ['enero', 'febrero', 'marzo', 'abril', 'mayo', 'junio', 'julio', 'agosto', 'septiembre', 'octubre', 'noviembre', 'diciembre'],
        monthNamesShort: ['ene', 'feb', 'mar', 'abr', 'may', 'jun', 'jul', 'ago', 'sep', 'oct', 'nov', 'dic'],
        today: 'Hoy',
        clear: 'Claro'
    });

    const footer = (
        <span>
         
            <Button label="Buscar" icon="pi pi-check" />
        </span>
    );
    
    useEffect(() => {
        countryservice.getCountries().then(data => setCountries(data));
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    console.log(countries);
    const searchCountry = (event) => {
        setTimeout(() => {
            let _filteredCountries;
            if (!event.query.trim().length) {
                _filteredCountries = [...countries];
            }
            else {
                _filteredCountries = countries.filter((country) => {
                    return country.name.toLowerCase().startsWith(event.query.toLowerCase());
                });
            }

            setFilteredCountries(_filteredCountries);
        }, 250);

    }

        

    const itemTemplate = (item) => {
        return (
            <div className="country-item">
                <img alt={item.name} src={`images/flag/flag_placeholder.png`} onError={(e) => e.target.src = 'https://www.primefaces.org/wp-content/uploads/2020/05/placeholder.png'} className={`flag flag-${item.code.toLowerCase()}`} />
                <div>{item.name}</div>
            </div>
        );
    }
    return (
  
  <div class="card">

 <Card style={{ width: '25em' }} footer={footer}>
 
  <h5>Vuelos </h5>

    <div class="formgrid grid" >
    
        <div class="field col-9 md:col-6">
        <label for="origen6">ORIGEN</label>
            <AutoComplete dropdown value={selectedCountry1} suggestions={filteredCountries} completeMethod={searchCountry} field="name"  forceSelection itemTemplate={itemTemplate} onChange={(e) => setSelectedCountry1(e.value)} aria-label="Countries" /> 
            
        </div>
        <div class="field col-9 md:col-6">
        <label for="destino6">DESTINO</label>
        <AutoComplete dropdown value={selectedCountry2} suggestions={filteredCountries} completeMethod={searchCountry} field="name2"  forceSelection itemTemplate={itemTemplate} onChange={(e) => setSelectedCountry2(e.value)} aria-label="Countries2" />
           
        </div>
       
        <div class="field col-12 md:col-6">
            <label for="city">FECHA IDA      </label>
            <Calendar id="icon"  value={date2} onChange={(e) => setDate2(e.value)} showIcon   />
        </div>

        <div className="field col-12 md:col-6">
            <label htmlFor="icon">FECHA REGRESO</label>
            <Calendar id="icon"  value={date3} onChange={(e) => setDate3(e.value)} showIcon   />
            </div>
        <div class="field col-12 md:col-6">
            <label for="city">ADULTOS</label>
            <input id="city" type="text" class="text-base text-color surface-overlay p-2 border-1 border-solid surface-border border-round appearance-none outline-none focus:border-primary w-full"/>
        </div>
       
        <div class="field col-12 md:col-3">
            <label for="zip">NINOS</label>
            <input id="zip" type="text" class="text-base text-color surface-overlay p-2 border-1 border-solid surface-border border-round appearance-none outline-none focus:border-primary w-full"/>
        </div>

        <Connect/>
    </div>
   
</Card>

  </div>


  
  );
}