import React, { useState, useRef } from 'react';
import { Steps } from 'primereact/steps';
import { Toast } from 'primereact/toast';
import Buscar from './buscador';
//import './Steps.css';

export default function Step () {
    const [activeIndex, setActiveIndex] = useState(1);
    const toast = useRef(null);
    const items = [
        {
            label: 'BUSCAR VUELOS',
            command: (event) => {
                toast.current.show({ severity: 'info', summary: 'Primer paso', detail: event.item.label });
                  
            }
        },
        {
            label: 'VER DISPONIBLES',
            command: (event) => {
                toast.current.show({ severity: 'info', summary: 'Segundo paso', detail: event.item.label });
                <Buscar/>
            }
        },
        {
            label: 'SELECCIONAR ITINERARIO',
            command: (event) => {
                toast.current.show({ severity: 'info', summary: 'Tercer Paso', detail: event.item.label });
            }
        },
        {
            label: 'PAGAR',
            command: (event) => {
                toast.current.show({ severity: 'info', summary: 'Paso final', detail: event.item.label });
            }
        }
    ];

    return (
        <div className="steps-demo">
            <Toast ref={toast}></Toast>

            <div className="card">
               
               <center> <h1>Sigue estos pasos para seleccionar tus vuelos:</h1></center> 
                <Steps model={items} activeIndex={activeIndex} onSelect={(e) => setActiveIndex(e.index)} readOnly={false} />
            </div>
        </div>
    );
}